export default {
  name: 'RadarChartSetter',
  components: { BColorPicker },
  props: {
    attribute: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      tempColor: ''
    }
  },
  methods: {
    addColor() {
      this.attribute.pieColor.push(this.tempColor)
    },
    delColor(index) {
      this.attribute.pieColor.splice(index, 1)
    }
  }
}
