<!-- eslint-disable vue/no-mutating-props -->
<script>
import BColorPicker from '@/components/BColorPicker.vue'
export default {
  name: 'PieSetter',
  components: { BColorPicker },
  props: {
    attribute: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      tempColor: ''
    }
  },
  methods: {
    addColor() {
      this.attribute.pieColor.push(this.tempColor)
    },
    delColor(index) {
      this.attribute.pieColor.splice(index, 1)
    }
  }
}
</script>

