<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div>
    <el-form labelWidth="100px" size="mini">
      <el-form-item label="">
        <el-switch v-model="attribute.showIndex" size="mini" />
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="attribute.thead" size="mini" />
      </el-form-item>
      <el-form-item v-show="attribute.thead" label="">
        <el-input-number
          v-model="attribute.theadHeight"
          style="width: 100%"
          controls-position="right"
          size="mini"
          :min="1"
          :max="800"
        />
      </el-form-item>
      <el-form-item v-show="attribute.thead" label="">
        <el-color-picker v-model="attribute.theadBg[0]" size="mini" />
        <el-color-picker v-model="attribute.theadBg[1]" size="mini" />
      </el-form-item>
      <el-form-item v-show="attribute.thead" label="">
        <BColorPicker :data="attribute" field="theadColor" size="mini" />
      </el-form-item>
      <el-form-item v-show="attribute.thead" label="">
        <el-input-number
          v-model="attribute.theadSize"
          controls-position="right"
          size="mini"
          style="width: 100%"
          :min="1"
          :max="200"
        />
      </el-form-item>
      <el-form-item label="">
        <BColorPicker :data="attribute" field="tbodyColor" size="mini" />
      </el-form-item>
      <el-form-item label="">
        <el-input-number
          v-model="attribute.tbodySize"
          controls-position="right"
          size="mini"
          :min="1"
          style="width: 100%"
          :max="200"
        />
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="attribute.tbodyFontWeight" popper-class="big-screen-select">
          <el-option label="" value="lighter" />
          <el-option label="" value="normal" />
          <el-option label="" value="bold" />
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="attribute.tbodyFontStyle" popper-class="big-screen-select">
          <el-option label="" value="normal" />
          <el-option label="" value="italic" />
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="attribute.tbodyFontFamily" popper-class="big-screen-select">
          <el-option v-for="(item, index) in textFamilyList" :key="index + item" :label="item" :value="item" />
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <BColorPicker :data="attribute" field="oddRowBg" size="mini" show-alpha />
      </el-form-item>
      <el-form-item label="">
        <BColorPicker :data="attribute" field="evenRowBg" size="mini" show-alpha />
      </el-form-item>
      <el-form-item label="">
        <el-input-number
          v-model="attribute.showLine"
          controls-position="right"
          style="width: 100%"
          size="mini"
          :min="1"
          :max="200"
        />
      </el-form-item>
      <el-form-item label="(s)">
        <el-input-number
          v-model="attribute.interval"
          controls-position="right"
          style="width: 100%"
          :min="1"
          :max="100"
          :step="1"
        />
      </el-form-item>

      <div style="text-indent: 1em">
        <el-button size="mini" theme="primary" @click="addCol"></el-button>
      </div>
      <el-table style="font-size: 12px; margin-top: 10px" size="mini" :data="attribute.columns" @row-click="editRow">
        <el-table-column label="" prop="colKey" />
        <el-table-column label="" prop="title" />
        <el-table-column label="" prop="width" />
      </el-table>
    </el-form>
    <el-dialog :title="currentRow.id ? '' : ''" :visible.sync="modelShow" :append-to-body="true" width="400px">
      <el-form labelWidth="100px" size="mini">
        <el-form-item label="">
          <el-input v-model="currentRow.colKey" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currentRow.title" />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="currentRow.type" popper-class="big-screen-select">
            <el-option label="" value="text" />
            <el-option label="" value="img" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            v-model="currentRow.width"
            style="width: 100%"
            controls-position="right"
            :min="0"
            :max="1000"
          />
        </el-form-item>
        <el-form-item v-show="currentRow.id">
          <el-button theme="danger" @click="delCol"></el-button>
        </el-form-item>
        <el-button v-show="!currentRow.id" @click="confirmRow"></el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import BColorPicker from '@/components/BColorPicker.vue'
export default {
  name: 'ScrollTable2Setter',
  components: { BColorPicker },
  props: {
    attribute: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      columns: [
        { colKey: 'colKey', title: '' },
        { colKey: 'title', title: '' },
        { colKey: 'width', title: '' }
      ],
      modelShow: false,
      currentIndex: 0,
      textFamilyList: Object.freeze([
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        'SourceHanSans-Medium'
      ]),
      currentRow: {}
    }
  },
  methods: {
    confirmRow() {
      if (!this.currentRow.colKey || !this.currentRow.title) {
        this.$message.error('')
        return
      }
      const id = new Date().getTime()
      // eslint-disable-next-line vue/no-mutating-props
      this.attribute.columns.push(Object.assign(this.currentRow, { id: id }))
      this.modelShow = false
    },
    addCol() {
      this.currentRow = { width: 0 }
      this.modelShow = true
    },
    editRow(row) {
      this.currentRow = row
      this.currentIndex = this.attribute.columns.indexOf(row)
      this.modelShow = true
    },
    delCol() {
      // eslint-disable-next-line vue/no-mutating-props
      this.attribute.columns.splice(this.currentIndex, 1)
      this.modelShow = false
    }
  }
}
</script>

<style scoped></style>
