import { Message, MessageBox, Notification, Loading } from 'element-ui'

let loadingInstance

export default {
  // 
  msg(content) {
    Message.info({ message: content, offset: 50 })
  },
  // 
  msgError(content) {
    Message.error({ message: content, offset: 50 })
  },
  // 
  msgSuccess(content) {
    Message.success({ message: content, offset: 50 })
  },
  // 
  msgWarning(content) {
    Message.warning({ message: content, offset: 50 })
  },
  // 
  alert(content) {
    MessageBox.alert(content, '')
  },
  // 
  alertError(content) {
    MessageBox.alert(content, '', { type: 'error' })
  },
  // 
  alertSuccess(content) {
    MessageBox.alert(content, '', { type: 'success' })
  },
  // 
  alertWarning(content) {
    MessageBox.alert(content, '', { type: 'warning' })
  },
  // 
  notify(content) {
    Notification.info(content)
  },
  // 
  notifyError(content) {
    Notification.error(content)
  },
  // 
  notifySuccess(content) {
    Notification.success(content)
  },
  // 
  notifyWarning(content) {
    Notification.warning(content)
  },
  // 
  confirm(content) {
    return MessageBox.confirm(content, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
  },
  // 
  loading(content = '', el, spinner = 'el-icon-loading', background = 'rgba(255, 255, 255, .5)') {
    loadingInstance = Loading.service({
      target: el || document.querySelector('body'),
      lock: true,
      text: content,
      spinner,
      background
    })
    return loadingInstance
  },
  // 
  closeLoading() {
    loadingInstance.close()
  }
}
